import { z } from "./lorenz-attractor.cul.js";import { y } from "./lorenz-attractor.cul.js";import { x } from "./lorenz-attractor.cul.js";import { i } from "./lorenz-attractor.cul.js";import { t } from "./lorenz-attractor.cul.js";import { c } from "./lorenz-attractor.cul.js";import { b } from "./lorenz-attractor.cul.js";import { a } from "./lorenz-attractor.cul.js"; // based off C code in https://www.algosome.com/articles/lorenz-attractor-programming-code.html
// also includes useful background

export const a_ = ({ a_in }) => a_in;
export const b_ = ({ b_in }) => b_in;
export const c_ = ({ c_in }) => c_in;

export const t_ = ({}) => 0.01; // time step

export const i_ = ({ i_in }) => i_in; // iteration

export const x_ = ({ i_in, a_in, b_in, c_in }) => {
  if (i({ i_in }) == 0) return 0.1;
  return (
    x({ a_in, b_in, c_in, i_in: i({ i_in }) - 1 }) +
    t({}) * a({ a_in }) * (y({ a_in, b_in, c_in, i_in: i({ i_in }) - 1 }) - x({ a_in, b_in, c_in, i_in: i({ i_in }) - 1 })));

};

export const y_ = ({ i_in, a_in, b_in, c_in }) => {
  if (i({ i_in }) == 0) return 0;
  return (
    y({ a_in, b_in, c_in, i_in: i({ i_in }) - 1 }) +
    t({}) * (
    x({ a_in, b_in, c_in, i_in: i({ i_in }) - 1 }) * (b({ b_in }) - z({ a_in, b_in, c_in, i_in: i({ i_in }) - 1 })) -
    y({ a_in, b_in, c_in, i_in: i({ i_in }) - 1 })));

};

export const z_ = ({ i_in, a_in, b_in, c_in }) => {
  if (i({ i_in }) == 0) return 0;
  return (
    z({ a_in, b_in, c_in, i_in: i({ i_in }) - 1 }) +
    t({}) * (
    x({ a_in, b_in, c_in, i_in: i({ i_in }) - 1 }) * y({ a_in, b_in, c_in, i_in: i({ i_in }) - 1 }) - c({ c_in }) * z({ a_in, b_in, c_in, i_in: i({ i_in }) - 1 })));

};